      <ul class="fitter_multiple">
        <li @click="clickSort(index)" v-for="(item, index) in sortList" :key="index" :class="{'select': sortData == index}">{{ item.categoryName }}</li>
        <!-- <li>热播榜</li>
        <li>新上线</li> -->
      </ul>
      <ul class="fitter_type">
