      let filter = fitterUrl //+ '&' + this.sortName + '=' + this.sortID + '&' + this.areaName + '=' + this.areaID + '&' + this.yearsName + '=' + this.yearsID + '&' + this.contentName + '=' + this.contentID
      this.filter(filter)
