  methods: {
    back() {
      this.$router.push({
        path: '/index/recommend'
      })
    },
    clickSort(index) {
      this.sortData = index
      this.sortID = this.sortList[this.sortData].categoryId
      let filter = this.url + '&' + this.areaName + '=' + this.areaID + '&' + this.yearsName + '=' + this.yearsID + '&' + this.contentName + '=' + this.contentID + '&l=zh_CN'
      this.filterList(filter)
      // '&' + this.sortName + '=' + this.sortID +
    },
    clickArea(index) {
      this.areaData = index
      this.areaData = index
      this.areaID = this.areaList[this.areaData].categoryId
      let filter = this.url + '&' + this.areaName + '=' + this.areaID + '&' + this.yearsName + '=' + this.yearsID + '&' + this.contentName + '=' + this.contentID + '&l=zh_CN'
      this.filterList(filter)
    },
    clickYears(index) {
      this.yearsData = index
      this.yearsData = index
      this.yearsID = this.yearsList[this.yearsData].categoryId
