        // }

        try {
          this.area = res.data.data.list[2]
          this.areaName = res.data.data.list[2].categoryCode
        } catch (error) {

        }
        if (this.area) {
          let areas = {
            categoryId: '',//this.area.categoryId,
            categoryName: this.area.categoryName
          }
          this.areaList = res.data.data.list[2].list
          this.areaList.splice(0, 0, areas)
        }

        try {
          this.years = res.data.data.list[1]
          this.yearsName = res.data.data.list[1].categoryCode
        } catch (error) {

        }
        if (this.years) {
          let yearss = {
            categoryId: '',//this.years.categoryId,
            categoryName: this.years.categoryName
          }
          this.yearsList = res.data.data.list[1].list
          this.yearsList.splice(0, 0, yearss)
        }

        try {
          this.content = res.data.data.list[0]
          this.contentName = res.data.data.list[0].categoryCode
        } catch (error) {

        }
        if (this.content) {
          let contents = {
            categoryId: '',//this.content.categoryId,
            categoryName: this.content.categoryName
          }
          this.contentList = res.data.data.list[0].list
          this.contentList.splice(0, 0, contents)
          for (let i = 0; i < this.contentList.length; i++) {
            if (this.tagName == this.contentList[i].categoryName) {
              this.contentData = i
            }
          }
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    filterList(listUrl) {
      console.log(listUrl)
      this.axios.get(listUrl).then((res) => {
        this.list = []
        console.log(res)
        if (localStorage.getItem('city') == 0) {
          for (let i = 0; i < res.data.data.assets.length; i++) {
            if (res.data.data.assets[i].playArea == '2') {
              this.list.push(res.data.data.assets[i])
            } else if (res.data.data.assets[i].playArea == '0') {
              this.list.push(res.data.data.assets[i])
            }
          }
        } else if (localStorage.getItem('city') == 1 || localStorage.getItem('city') == 4) {
          for (let i = 0; i < res.data.data.assets.length; i++) {
            if (res.data.data.assets[i].playArea == '1') {
              this.list.push(res.data.data.assets[i])
            } else if (res.data.data.assets[i].playArea == '0') {
              this.list.push(res.data.data.assets[i])
            }
          }
        } else if (localStorage.getItem('city') == null) {
          for (let i = 0; i < res.data.data.assets.length; i++) {
            // if (res.data.data.assets[i].playArea == '1') {
            //   this.list.push(res.data.data.assets[i])
            // } else if (res.data.data.assets[i].playArea == '0') {
              this.list.push(res.data.data.assets[i])
            // }
          }
        }
        // this.list = res.data.data.assets
      }).catch((error) => {
        console.log(error)
      })
    },
    toDetail(index, event) {
      let detailUrl= this.list[index].jsonUrl
      this.$router.push({
        name: 'detail',
        params: {
          jsonUrl: detailUrl
        }
      })
    }
