  height: 3.06rem;
  /* background-image: url('../../assets/images/activity_img.png'); */
  background-size: cover;
  background-position: center;
  border-radius: .2rem;
}
.fitter_list_title {
  margin-top: .1rem;
  font-size: .24rem;
  color: #636055;
  text-align: center;
  height: .7rem;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
}

.no_fitter {
  text-align: center;
  font-size: .3rem;
}
</style>



